//하나의 최상위 태그만 써야한다.
//subject가 this 우리가 원하는 걸 출력함

class App extends Component { // class 안에는 strict mode가 적용됨 그래서 this는 undefined가 바잉됨
  constructor(props){
    super(props);
    this.state = {
      mode : 'create',
      selected_content_id : 2,
      subject : {title:'web', sub:'world wid web!'},
      welcome : {title:'Welcom', desc:'Hello, React!!'}, 
      contents:[
        {id:1, title:'html', desc:'hteml is...'},
        {id:2, title:'css', desc:'css is...'},
        {id:3, title:'javascript', desc:'javascript is...'}
      ]
    }
  }// 가장 먼저 실행됨 초기화 담당
  
  render(){ //props 혹은 state가 변경되면 해당되는 component에 render가 변경된다.
    var _title, _desc, _article = null;
    if(this.state.mode === 'welcome'){
        _title = this.state.welcome.title;
        _desc = this.state.welcome.desc;
        _article = <ReadArticle title={_title} desc={_desc}></ReadArticle>;
    }else if(this.state.mode === 'read'){
        // for(var i = 0; i < this.state.contents.length;i++){
        //   var data = this.state.contents[i];
        //   if(data.id == this.state.selected_content_id){
        //     _title = data.title
        //     _desc = data.desc
        //     break;
        //   }
        // }
        // 2021 05 07
        var num = (this.state.selected_content_id-1);
        var data = this.state.contents[num];
        _title = data.title;
        _desc = data.desc;
        _article = <ReadArticle title={_title} desc={_desc}></ReadArticle>;
    }else if(this.state.mode == "create"){
        _article = <CreateContent onSubmit={function(_title, _desc){
        // set state add content to this.state.contents
        var num = this.state.contents.length+1;
        console.log(_title, _desc);
        var _contents = this.state.contents.concat({id:num, title: _title, desc:_desc}) 
        // push는 원본을 수정한다 concat은 원본값에 추가한걸 리턴함 this.porps.data가 수정되면 toc에 슈드에 지장이 생김 비슷한걸로 object assign, array from 이있다
        // 둘다 원본을 복사 해서 사용한다
        this.setState({
          contents:_contents
        }); 
        // setState는 최초에 렌더링된 값을 변경하는 용도로 사용된다.
        // 하지만 render가 끝나고 변경되기 때문에 값이 바로 변경되지는 않는다.
        console.log(this.state.contents);
      }.bind(this)}  title={this.state.mode}></CreateContent>
    }else if(this.state.mode == "update"){

    }
      console.log('render:::', this);
    return(
      <div className = "App">
        {/* <header>
          <h1><a href="/" onClick={function(e){
            console.log('event:::', this);
            e.preventDefault();
            return;
            console.log(e);
            e.preventDefault(); //이벤트 동작 막음
            //this.state.mode = 'welcome'; 작동 불가 리엑트는 소스가 변경되면 render를 하는데 
            this.setState({
              mode : 'welcome'
            }) //bind를 안해주면 클릭한 것 window를 가리킨다.
            // 그래서 바인드를 함 app안에 메소드 처럼 동작하기 위한 것

          }.bind(this)}>{this.state.subject.title}</a></h1>
          {this.state.subject.sub}
        </header> */}
        <Subject title={this.state.subject.title} sub ={this.state.subject.sub} onChangePage = {function(){
            this.setState({
              mode : 'welcome'
            })
        }.bind(this)}></Subject>
        <TOC onChangePage = {function(id){
            this.setState({mode:'read', selected_content_id:Number(id)});
            //2021 05 07
        }.bind(this)} data={this.state.contents}></TOC>
        <Control onChangeMode = {function(_mode){
          this.setState({
            mode : _mode
          });
        }.bind(this)}></Control>
        {/* <ReadArticle title={_title} desc={_desc}></ReadArticle> */}
        {_article}
      </div>
    );
  }
}
// <Article title={_title} desc={_desc}></Article> article에서는 title props를 통해 값을 주입이 가능 하다 사용자가 하지만 변경은 불가능 하다 컴포넌트 안에서 
// props state 둘다 render를 가동시켜서 적재적소에 사용하면 된다
// component 안에 state가 있다. 내부 동기 방식 버튼을 누르면 화면이 변경되거나 상태가 변경되는 조치
// 외부에서는 props를통해 접근한다. 스마트폰에 버튼 
// 상위 컴포넌트 app -> 하위 컴포넌트 toc, subject 등등 에게 값전달 혹은 조작 -> props
// 하위 컴포넌트가 상위 컴포넌트에 값을 변경하고 싶으면 이벤트로 

