import React, {Component} from 'react';
//여러가지 엘리먼트가 자동생성되면 오류가 난다. 그래서 key를 주면됨
class TOC extends Component {
    shouldComponentUpdate(newProps, newState){
      if(this.props.data === newProps.data){
        return false;
      }else{
        console.log(newProps);
        return true;
      }
      
    }// props와 state가 바뀐걸 newprops 와 newState로 받음 props혹은state가 변경될떄 호출됨
    render(){
      console.log('toc render!!!!')
      var data = this.props.data;
      var lists = [];
      for(var i = 0; i < data.length; i++)
         lists.push(
         <li key={data[i].id}> 
            <a data-id={data[i].id} onClick = {function(e){
              e.preventDefault();
              this.props.onChangePage(e.target.dataset.id); // 2021 05 07
            }.bind(this)}  href={"/content/"+data[i].id}>{data[i].title}</a>
         </li>);
      return(
        <nav>
          <ul>
             {lists}
          </ul>
        </nav>
      );
    }
  }

  export default  TOC; //toc를 선택해서 내보냄 